/* App.css */

body {
  font-family: 'Press Start 2P', cursive;
  background-color: #000; /* Dark background */
  color: #000; /* Black text */
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

.App {
  text-align: center;
  padding: 5%;
  border-radius: 12px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.4); /* Updated shadow color */
  background: linear-gradient(45deg, #1a1a1a, #333333);
  width: 90%;
  max-width: 400px;
  margin: 0 auto;
  border: 6px solid #000; /* Updated border color */
  animation: borderAnimation 2s infinite alternate;
}

@keyframes borderAnimation {
  0% {
    border-color: #000; /* Updated border color */
  }
  100% {
    border-color: #1a1a1a;
  }
}

button {
  font-size: 3vw; /* Responsive font size */
  padding: 3% 5%; /* Responsive padding */
  margin: 2% 0;
  cursor: pointer;
  border: 2px solid #000; /* Updated border color */
  border-radius: 8px;
  background: linear-gradient(45deg, #1a1a1a, #333333);
  color: #000; /* Updated text color */
  transition: background-color 0.3s ease-in-out, transform 0.2s ease-in-out;
  transform: scale(1);
}

button:hover {
  background: linear-gradient(45deg, #333333, #1a1a1a);
  transform: scale(1.05);
}

.counts-container {
  margin-top: 3%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

table {
  width: 48%;
  border-collapse: collapse;
  text-align: left;
  margin-bottom: 3%;
}

th, td {
  padding: 3%;
  border-bottom: 1px solid #000; /* Updated border color */
  font-size: 2.5vw; /* Responsive font size */
  color: #000; /* Updated text color */
}

.reset-button {
  font-size: 3vw; /* Responsive font size */
  padding: 2% 4%; /* Responsive padding */
  margin-top: 2%;
  cursor: pointer;
  border: 2px solid #000; /* Updated border color */
  border-radius: 8px;
  background: linear-gradient(45deg, #1a1a1a, #333333);
  color: #000; /* Updated text color */
  transition: background-color 0.3s ease-in-out, transform 0.2s ease-in-out;
  transform: scale(1);
}

.reset-button:hover {
  background: linear-gradient(45deg, #333333, #1a1a1a);
  transform: scale(1.05);
}

.total-rolls {
  margin-top: 3%;
  font-size: 2.5vw; /* Responsive font size */
  color: #000; /* Updated text color */
}

.last-rolled {
  margin-top: 2%;
  font-size: 2vw; /* Responsive font size */
  color: #000; /* Updated text color */
}

h1 {
  color: #000; /* Updated text color */
  font-size: 3vw; /* Responsive font size */
  margin-bottom: 3%;
}
